import React from 'react'
import clsx from 'clsx'
import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import Button from '@material-ui/core/Button'
import List from '@material-ui/core/List'
import Divider from '@material-ui/core/Divider'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import InboxIcon from '@material-ui/icons/MoveToInbox'
import MailIcon from '@material-ui/icons/Mail'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import Fab from '@material-ui/core/Fab'
import AddIcon from '@material-ui/icons/Add'
import Hidden from '@material-ui/core/Hidden'
import { fontFamily } from '@material-ui/system'
import { Link } from 'react-router-dom'

const useStyles = makeStyles({
  list: {
    width: 250,
    height: '100vh'
  },
  fullList: {
    width: 'auto',
    height: '100vh',
    '& .MuiList-padding':{
      height: '100vh',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      '& .MuiListItem-button':{
        textAlign: 'center'
      }
    }
  },
  // fab: {
  //   background: '#454545'
  // },
  menuButton: {
  },
  '.MuiFab-primary':{
    background: '#454545'
  }
})

const listStyles = makeStyles({
  root: {
    height: '100vh',
    
    '& .MuiTypography-body1':{
      fontFamily: 'sofia-pro',
      fontSize: 24
    }
  },
})

export default function FabNav() {
  const classes = useStyles()
  const listClasses = listStyles()
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  })



  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return
    }

    setState({ ...state, [anchor]: open })
  }

  const HomeLink = props => <Link to={'/'} {...props} />
  const WorkLink = props => <Link to={'/work'} {...props} />
  const PlayLink = props => <Link to={'/play'} {...props} />
  const ContactLink = props => <Link to={'/contact'} {...props} />
  const links = [HomeLink, WorkLink, PlayLink, ContactLink]

  const list = (anchor) => (

    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {['Home', 'Work', 'Play', 'Conatct'].map((text, index) => (
          <ListItem
            button key={text}
            component={links[index]}
            classes={listClasses}>
          {/* <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
            <ListItemIcon/> */}
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
      <Divider />
      {/* <List>
        {['All mail', 'Trash', 'Spam'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List> */}
    </div>
  )

  return (
    <div>
      {['bottom'].map((anchor) => (
        <React.Fragment key={anchor}>
          {/* <Button onClick={toggleDrawer(anchor, true)}>{anchor}</Button> */}
          <Hidden smUp>
            <Fab 
              classes={classes.fab}
              color='primary'
              aria-label='navigation'>
              <IconButton
                onClick={toggleDrawer(anchor, true)}
                edge='start'
                className={classes.menuButton}
                color='inherit'
                aria-label='menu'>
              </IconButton>
              {/* <AddIcon /> */}
            </Fab>
            <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
              {list(anchor)}
            </Drawer>
          </Hidden>
        </React.Fragment>
      ))}
    </div>
  )
}
